import React from 'react';
import Img from 'gatsby-image';

import { screen } from '../constants';
import Container from './Container';

export default function BlockLogos({ pretitle, title, data }) {
  return (
    <section className="block-logos">
      <Container>
        <div className="block-logos__wrap">
          {pretitle && (
            <div className="app__pretitle block-logos__pretitle">
              {pretitle}
            </div>
          )}
          {title && (
            <div className="app__title block-logos__title">{title}</div>
          )}
          <div className="block-logos__content">
            {data.map(({ image, name, offsetTop = 0 }, index) => (
              <Img
                key={index}
                fluid={image}
                imgStyle={{ objectFit: 'contain' }}
                alt={name}
                className="app__image block-logos__image"
                style={{ top: offsetTop }}
              />
            ))}
          </div>
        </div>
      </Container>
      <style jsx>{`
        .block-logos__wrap {
          margin: 24px 0 60px;
        }
        .block-logos__content {
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
        }

        .block-logos__title {
          text-align: center;
          font-style: normal;
          font-weight: normal;
          font-size: 36px;
          line-height: 42px;
          max-width: 715px;
          text-align: center;
          margin-right: auto;
          margin-left: auto;
        }

        .block-logos__pretitle {
          text-align: center;
        }

        @media (max-width: ${screen.mobileMax}px) {
          .block-logos__title {
            max-width: 400px;
          }
          .block-logos__content {
            align-items: center;
            flex-direction: column;
          }
        }
      `}</style>
      <style jsx global>{`
        .block-logos__image {
          margin: 25px 20px;
          flex: 1;
          max-height: 84px;
        }
        @media (max-width: ${screen.mobileMax}px) {
          .block-logos__image {
            min-width: 150px;
          }
        }
      `}</style>
    </section>
  );
}
