import React from 'react';
import { graphql } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layout from '../components/Layout';
import Container from '../components/Container';
import BlockShopifyHubspotForm from '../components/BlockShopifyHubspotForm';
import Footer from '../components/Footer';
import BlockLogos from '../components/BlockLogos';
import SpaceDivider from '../components/SpaceDivider';

import { fonts } from '../constants';
import { edgesByName } from '../utils/gql';

export const pageQuery = graphql`
  {
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
  }
`;

export default function ShopifyFormPage({ data }) {
  const companies = edgesByName(data.companies, node => node.image.fluid);
  return (
    <Layout>
      <GatsbySeo
        title="Shopify checkout integration for net terms"
        description="Sign up to be notified when we complete the Shopify check-out integration for Resolve&#39;s net terms"
      />
      <Container>
        <BlockShopifyHubspotForm />
        <SpaceDivider height={100} />
        <BlockLogos
          pretitle="Trusted by innovative companies"
          data={[
            {
              image: companies['Linus Bike'],
              name: 'Linus Bike',
              offsetTop: 15,
            },
            {
              image: companies['ConEquip'],
              name: 'ConEquip',
              offsetTop: 10,
            },
            {
              image: companies['Jupiter Pro Parts'],
              name: 'Jupiter Pro Parts',
              offsetTop: -10,
            },
            {
              image: companies['Ozark LED'],
              name: 'Ozark LED',
              offsetTop: 0,
            },
            {
              image: companies['Archipelago Lighting'],
              name: 'Archipelago Lighting',
              offsetTop: 10,
            },
            {
              image: companies['Go Materials'],
              name: 'Go Materials',
              offsetTop: -10,
            },
          ]}
        />
      </Container>
      <Footer darkSkin />
      <style jsx>{`
        .shopify_form__container {
          max-width: 600px;
          width: 100%;
          margin: 50px auto;
        }
        .shopify_form__container * {
          box-sizing: border-box;
        }
        .shopify_form__title {
          font-family: ${fonts.halyardDisplay};
          font-size: 28px;
          font-weight: 600;
          line-height: 1.43;
          letter-spacing: 0px;
          text-align: center;
        }
      `}</style>
    </Layout>
  );
}
