import React, { useEffect, useState } from 'react';
import { navigate } from 'gatsby';
import { useFormik } from 'formik';
import { object, string } from 'yup';
import { useLocation } from '@reach/router';

import Button from './Button';
import Input from './Input';
import Select from './Select';

import { fonts, colors } from '../constants';
import { identifySegment, trackSegment } from '../utils/segment';
import { getCampaign } from '../utils/utm';

const getHubspotContext = () => {
  const hutk = document.cookie.replace(
    /(?:(?:^|.*;\s*)hubspotutk\s*=\s*([^;]*).*$)|^.*$/,
    '$1'
  );

  return {
    hutk: hutk || undefined,
    pageUri: `${window.location.origin}${window.location.pathname}`,
    pageName: document.title,
  };
};

const getDefaultHiddenFields = () => {
  return [
    {
      name: 'lead_type__c',
      value: 'Inquiry',
    },
    {
      name: 'inquiry_type__c',
      value: 'Web Shopify Form',
    },
    {
      name: 'leadsource',
      value: 'Inbound',
    },
    {
      name: 'lead_source',
      value: 'Web Shopify Form',
    },
  ];
};

export default function BlockShopifyHubspotForm() {
  const [error, setError] = useState(false);
  const [ipAddress, setIpAddress] = useState();
  const { state: locationState } = useLocation();

  useEffect(() => {
    const getIp = async () => {
      try {
        const res = await fetch(
          `https://api.ipstack.com/check?access_key=${process.env.GATSBY_IP_STACK_ACCESS_KEY}`
        );
        const userInfo = await res.json();

        if (userInfo.success === false) {
          throw userInfo.error;
        }

        setIpAddress(userInfo.ip);
      } catch (e) {}
    };

    getIp();
  }, []);

  const formIndex =
    locationState?.formIndex && !isNaN(locationState?.formIndex)
      ? locationState?.formIndex
      : 0;

  useEffect(() => {
    const script = document.createElement('script');
    script.src = '//js.hsforms.net/forms/v2.js';
    document.body.appendChild(script);

    script.addEventListener('load', () => {
      if (window.hbspt) {
        window.hbspt.forms.create({
          portalId: '8982602',
          formId: 'dfbf8cb9-124a-44d1-b7b0-cf78c4a3b3ce',
          target: '#shopify-hubspot-form',
        });
      }
    });
  }, []);

  const onSubmitIndex0 = async (values, { setSubmitting }) => {
    setError(false);

    try {
      const res = await fetch(
        'https://api.hsforms.com/submissions/v3/integration/submit/8982602/dfbf8cb9-124a-44d1-b7b0-cf78c4a3b3ce',
        {
          headers: {
            'Content-Type': 'application/json',
          },
          method: 'POST',
          body: JSON.stringify({
            fields: [
              {
                name: 'firstname',
                value: values.firstName,
              },
              {
                name: 'lastname',
                value: values.lastName,
              },

              {
                name: 'company',
                value: values.companyName,
              },
              {
                name: 'email',
                value: values.email,
              },
              {
                name: 'website__c',
                value: values.website,
              },
              {
                name: 'accounting_platform',
                value: values.accountingPlatform,
              },
              ...getDefaultHiddenFields(),
            ],
            context: {
              ...getHubspotContext(),
              ipAddress,
            },
          }),
        }
      );
      const response = await res.json();
      if (response.status === 'error') {
        throw response.message;
      }

      navigate('', { state: { formIndex: 1 } });
      setSubmitting(false);

      identifySegment(values);
      trackSegment('Shopify sign-up form submitted', {
        ...values,
        context: {
          campaign: getCampaign(),
        },
      });
    } catch (e) {
      setError(true);
    }
  };

  const formikIndex0 = useFormik({
    initialValues: {
      firstName: '',
      lastName: '',
      email: '',
      companyName: '',
      website: '',
      accountingPlatform: '',
    },
    validationSchema: object().shape({
      firstName: string().trim().required('Required'),
      lastName: string().trim().required('Required'),
      email: string()
        .trim()
        .lowercase()
        .email('Invalid email')
        .required('Required'),
      companyName: string().trim().required('Required'),
      website: string().trim().required('Required'),
      accountingPlatform: string().trim().required('Required'),
    }),
    onSubmit: onSubmitIndex0,
  });

  useEffect(() => {
    if (formIndex !== 0 && !formikIndex0.values.email) {
      navigate('', { state: { formIndex: 0 } });
    }
  });

  return (
    <div className="shopify_form__container">
      {error ? (
        <div className="shopify_form_error">Something went wrong</div>
      ) : null}
      {formIndex === 0 ? (
        <>
          <div className="shopify_form__title">Get notified</div>
          <div className="shopify_form__subtitle">
            Sign up to be notified when we complete the Shopify checkout
            integration for Resolve's net terms
          </div>
          <form onSubmit={formikIndex0.handleSubmit}>
            {formikIndex0.isSubmitting ? (
              <div className="shopify_form__form-loading-mask" />
            ) : null}
            <div className="shopify_form__form_row">
              <div className="shopify_form__form_col">
                <Input
                  id="firstName"
                  name="firstName"
                  type="text"
                  error={
                    formikIndex0.touched.firstName
                      ? formikIndex0.errors.firstName
                      : undefined
                  }
                  value={formikIndex0.values.firstName}
                  onChange={formikIndex0.handleChange}
                  onBlur={formikIndex0.handleBlur}
                  label="First name"
                  placeholder="Enter first name"
                />
              </div>
              <div className="shopify_form__form_col">
                <Input
                  id="lastName"
                  name="lastName"
                  type="text"
                  error={
                    formikIndex0.touched.lastName
                      ? formikIndex0.errors.lastName
                      : undefined
                  }
                  value={formikIndex0.values.lastName}
                  onChange={formikIndex0.handleChange}
                  onBlur={formikIndex0.handleBlur}
                  label="Last name"
                  placeholder="Enter last name"
                />
              </div>
            </div>
            <div className="shopify_form__form_row">
              <div className="shopify_form__form_col">
                <Input
                  id="companyName"
                  name="companyName"
                  type="text"
                  error={
                    formikIndex0.touched.companyName
                      ? formikIndex0.errors.companyName
                      : undefined
                  }
                  value={formikIndex0.values.companyName}
                  onChange={formikIndex0.handleChange}
                  onBlur={formikIndex0.handleBlur}
                  label="Company name"
                  placeholder="Enter company name"
                />
              </div>
              <div className="shopify_form__form_col">
                <Input
                  id="email"
                  name="email"
                  type="text"
                  error={
                    formikIndex0.touched.email
                      ? formikIndex0.errors.email
                      : undefined
                  }
                  value={formikIndex0.values.email}
                  onChange={formikIndex0.handleChange}
                  onBlur={formikIndex0.handleBlur}
                  label="Email"
                  placeholder="Enter your email"
                />
              </div>
            </div>
            <div className="shopify_form__form_row">
              <div className="shopify_form__form_col">
                <Input
                  id="website"
                  name="website"
                  type="text"
                  error={
                    formikIndex0.touched.website
                      ? formikIndex0.errors.website
                      : undefined
                  }
                  value={formikIndex0.values.website}
                  onChange={formikIndex0.handleChange}
                  onBlur={formikIndex0.handleBlur}
                  label="Website"
                  placeholder="Enter Website"
                />
              </div>
              <div className="shopify_form__form_col">
                <Select
                  id="accountingPlatform"
                  name="accountingPlatform"
                  label="Accounting platform"
                  firstOptionLabel="Select platform"
                  error={
                    formikIndex0.touched.accountingPlatform
                      ? formikIndex0.errors.accountingPlatform
                      : undefined
                  }
                  value={formikIndex0.values.accountingPlatform}
                  onChange={formikIndex0.handleChange}
                  onBlur={formikIndex0.handleBlur}
                  options={[
                    {
                      value: 'Freshbooks',
                      label: 'Freshbooks',
                    },
                    {
                      value: 'Microsoft Dynamics 365',
                      label: 'Microsoft Dynamics 365',
                    },
                    {
                      value: 'Netsuite',
                      label: 'Netsuite',
                    },
                    {
                      value: 'Odoo',
                      label: 'Odoo',
                    },
                    {
                      value: 'Oracle',
                      label: 'Oracle',
                    },
                    {
                      value: 'Quickbooks Desktop',
                      label: 'Quickbooks Desktop',
                    },
                    {
                      value: 'Quickbooks Enterprise',
                      label: 'Quickbooks Enterprise',
                    },
                    {
                      value: 'Quickbooks Online',
                      label: 'Quickbooks Online',
                    },
                    {
                      value: 'Sage',
                      label: 'Sage',
                    },
                    {
                      value: 'SAP',
                      label: 'SAP',
                    },
                    {
                      value: 'Wave',
                      label: 'Wave',
                    },
                    {
                      value: 'Xero',
                      label: 'Xero',
                    },
                    {
                      value: 'Zoho',
                      label: 'Zoho',
                    },
                    {
                      value: 'Other',
                      label: 'Other',
                    },
                  ]}
                />
              </div>
            </div>
            <Button
              primary
              hasArrow
              isFullWidth
              type="submit"
              className="shopify_form__form_button"
              loading={formikIndex0.isSubmitting}
            >
              Submit
            </Button>
          </form>
          <div
            id="shopify-hubspot-form"
            className="shopify_form_hubspot_form"
          />
        </>
      ) : formIndex === 1 ? (
        <>
          <div className="shopify_form__title">
            Thank you for reaching out to us!
          </div>
          <p className="shopify_form__subtitle">
            Our team will reach out to you as soon as our Shopify integration is
            ready.
          </p>
        </>
      ) : null}
      <style jsx>{`
        .shopify_form__container {
          max-width: 616px;
          width: 100%;
          margin: 65px auto;
          position: relative;
        }
        .shopify_form__container * {
          box-sizing: border-box;
        }
        .shopify_form__form-loading-mask {
          position: absolute;
          width: 100%;
          height: 100%;
          top: 0;
          left: 0;
          background-color: ${colors.white};
          opacity: 0.3;
          z-index: 10000;
        }
        .shopify_form_error {
          font-family: ${fonts.halyardText};
          font-size: 17px;
          font-weight: 400;
          background-color: ${colors.redRibbon};
          color: ${colors.white};
          margin-bottom: 48px;
          padding: 16px;
        }
        .shopify_form__title {
          font-family: ${fonts.gilroy};
          font-size: 30px;
          font-weight: 600;
          letter-spacing: 0px;
          margin-bottom: 8px;
          line-height: 1.4;
          text-align: center;
        }
        .shopify_form__subtitle {
          font-size: 22px;
          color: ${colors.primary};
          font-family: ${fonts.halyardDisplay};
          font-weight: 300;
          margin-bottom: 32px;
          text-align: center;
        }
        .shopify_form__form_row {
          width: 100%;
          display: flex;
        }
        @media (max-width: 480px) {
          .shopify_form__form_row {
            flex-direction: column;
          }
        }
        .shopify_form__form_col {
          width: 100%;
        }
        .shopify_form__form_col:not(:first-of-type) {
          margin-left: 24px;
        }
        @media (max-width: 480px) {
          .shopify_form__form_col:not(:first-of-type) {
            margin-left: 0;
          }
        }
        .shopify_form_hubspot_form {
          display: none;
        }
      `}</style>
      <style jsx global>{`
        .shopify_form__form_button {
          margin-top: 28px;
        }
      `}</style>
    </div>
  );
}
